<template>
  <div class="videoDetails">
    <myBread :breadList="breadList" />
    <div class="card">
      <div class="left">
        <div class="video lightest-bg">
          <div class="video_window">
            <video
              id="myVideo"
              :src="videoDetails.source ? videoDetails.source.hd : ''"
              :poster="
                videoDetails.images ? videoDetails.images.appdetailbig : ''
              "
              loop
              controls
              onplaying="onplaying()"
            ></video>
            <!-- <div class="flay-of" v-show="isShow">
              <a href="javascript:void(0)" @click="handClick">
                <img :src="i==0 ? srcs:nosrc" class="ctrl">
              </a>
            </div>-->
          </div>
          <div class="textLh">
            <span class="dark-text">{{ videoDetails.title }}</span>
            <p class="light-text">{{ video.categoryTitle }}</p>
          </div>
          <div class="click">
            <ul>
              <li>
                <a href="javascript:void(0)" @click="share" class="light-text">
                  <span class="iconfont icon-fenxiang light-text"></span>
                  <span>分享</span>
                </a>
              </li>
              <li>
                <a
                  href="javascript:void(0)"
                  @click="collect"
                  class="light-text"
                >
                  <span
                    class="iconfont icon-shoucang light-text"
                    :class="[videoDetails.is_collect == 1 ? 'show' : '']"
                  ></span>
                  <span
                    v-html="videoDetails.is_collect === 1 ? '已收藏' : '收藏'"
                  ></span>
                </a>
              </li>
            </ul>
          </div>
        </div>
        <div class="textDetails lightest-bg">
          <span class="dark-text">视频详情</span>
          <p class="light-text">{{ videoDetails.description }}</p>
        </div>
      </div>
      <div class="right lightest-bg">
        <div
          class="myVideoList lightest-bg"
          v-for="(item, index) in (videoList || '').slice(0, 5)"
          :key="index"
        >
          <img
            :src="item.images ? item.images.appdetailsmall : defaultPcBackImg"
            class="title"
          />

          <a href="javascript:void(0)" @click="goVideo(item, index)">
            <span
              class="iconfont icon-bofang play-picture lightest-text"
            ></span>
          </a>
          <div class="myVideoList_text">
            <p class="dark-text">{{ item.title }}</p>
            <p class="light-text">{{ item.categoryTitle }}</p>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :title="qrTitle"
      :visible.sync="shareShow"
      id="scanDialoge"
      :close-on-click-modal="false"
      show-close
    >
      <QrcodeVue :value="qrText" size="200" class="qrCode" />
      <!-- <img id="qrCode" :src="imgUrl" alt> -->
      <p>{{ tipInfo }}</p>
    </el-dialog>
  </div>
</template>
<script>
import { updatedBreadCrumb } from "@/assets/js/util.js";
import myBread from "../../components/bread-crumb";
import QrcodeVue from "qrcode.vue";
export default {
  data() {
    return {
      onShow: false,
      qrTitle: "",
      tipInfo: "",
      wchatText: "",
      qrText: "",
      shareShow: false,
      id: "",
      videoDetails: ""
      // srcs: require("../../assets/img/bf.png"),
      // nosrc: require("../../assets/img/pause.png")
    };
  },
  created() {
    let title = sessionStorage.getItem("ClassicLabel");
    let data = {};
    data.path = this.$route.path;
    (data.name = title),
      // data.details=_this.list.title
      updatedBreadCrumb(data, 1, this);
  },
  mounted() {
    var v3 = document.getElementById("myVideo");
    var audio = document.getElementById("myAudio");
    (v3.onplaying = function() {
      audio.pause();
    }),
      this.getVideo();
  },
  methods: {
    // 获取视频详情
    getVideo() {
      var _this = this;
      //  let videoId=localStorage.getItem('videoId')
      //  console.log(videoId)
      var url = this.APIURL + "/classic/video/detail";
      this.$axios
        .post(
          url,
          { videoId: this.video.id },
          { headers: { SessionKey: this.SessionKey, key: this.key } }
        )
        .then(function({ data }) {
          _this.videoDetails = data.data;
        });
    },
    // 播放暂停图片切换
    // handClick(e) {
    //   var container = document.querySelector(".container");
    //   var ctrl = document.querySelector(".ctrl");
    //   var v3 = document.getElementById("myVideo");
    //   var audio = document.getElementById("myAudio");
    //   // var img = document.querySelector(".ctrl img");
    //   e.preventDefault();
    //   if (v3.paused) {
    //     v3.play();
    //     this.i = 1;
    //   } else {
    //     v3.pause();
    //     this.i = 0;
    //   }
    // },
    // onMouseEnter() {
    //   this.isShow = true;
    // },
    // onMouseLeave() {
    //   this.isShow = false;
    // },
    //视频详情右侧列表点击观看视频
    goVideo(item, index) {
      this.$store.commit("setVideo", item);
    },
    //分享二维码
    share() {
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/video/videoDetail?curId=" +
        this.videoDetails.id;
      this.qrTitle = "微信扫描二维码进行分享";
    },
    //收藏
    collect() {
      var _this = this;
      let data = {
        type: "collect",
        videoId: _this.videoDetails.id
      };
      var url = this.APIURL + "/classic/video/behavior";
      this.$axios
        .post(url, data, {
          headers: { SessionKey: this.SessionKey, key: this.key }
        })
        .then(function(data) {
          if (data.data.errorCode === 0) {
            _this.getVideo();
          } else {
            alert(data.data.msg);
            _this.$router.push({ path: "/login" });
          }
        });
    }
  },
  computed: {
    videoList() {
      return this.$store.state.videoList;
    },
    video() {
      return this.$store.state.video;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
    breadList() {
      return this.$store.state.breadList;
    }
  },
  watch: {
    videoList: function(val) {},
    video(val) {
      if (val) {
        this.getVideo();
      }
    }
  },
  components: {
    myBread,
    QrcodeVue
  }
};
</script>
<style lang="scss">
.videoDetails {
  .card {
    width: 1298px;
    margin: 22px auto;
    overflow: hidden;
    .left {
      float: left;
      .video {
        width: 1000px;
        height: 721px;
        opacity: 1;
        position: relative;
        overflow: hidden;
        box-shadow: 0.574px 0.819px 5px 0px rgba(204, 204, 204, 1);
        .textLh {
          span {
            display: inline-block;
            opacity: 1;
            font-size: 24px;
            font-family: Adobe Heiti Std;
            letter-spacing: 0px;
            margin: 18px 0 0 20px;
          }
          p {
            opacity: 1;
            font-size: 14px;
            font-family: Adobe Heiti Std;
            letter-spacing: 0px;
            margin: 10px 0 0 20px;
          }
        }
        .click {
          width: 664px;
          height: 36px;
          margin: 35px 0 0 20px;
          ul {
            margin: 0;
            padding: 0;
            line-height: 36px;
            overflow: hidden;
            li {
              width: 85px;
              height: 36px;
              list-style: none;
              float: left;
              margin-right: 30px;
              a {
                text-decoration: none;
                font-size: 16px;
                .iconfont {
                  font-size: 16px;
                  margin-right: 3px;
                  // vertical-align: middle;
                }
                .show {
                  color: #584945 !important ;
                }
              }
            }
          }
        }
        #myVideo {
          width: 100%;
          height: 554px;
          object-fit: fill;
        }
        .flay-of {
          position: absolute;
          top: 29%;
          left: 47%;
        }
      }
      .textDetails {
        width: 1000px;
        height: 219px;
        opacity: 1;
        box-shadow: 0.574px 0.819px 5px 0px rgba(204, 204, 204, 1);
        margin-top: 10px;
        span {
          opacity: 1;
          font-size: 18px;
          font-family: Adobe Heiti Std;
          letter-spacing: 0px;
          margin: 8px 0 0 20px;
          display: inline-block;
        }
        p {
          margin: 20px 0 0 20px;
          width: 944px;
          height: 118px;
          opacity: 1;
          font-size: 14px;
          font-family: Adobe Heiti Std;
          line-height: 24px;
          letter-spacing: 0px;
          overflow: auto;
        }
      }
    }
    .right {
      float: left;
      width: 286px;
      opacity: 1;
      margin-left: 10px;
      padding-top: 16px;
      .myVideoList {
        width: 269px;
        height: 167px;
        box-shadow: 0.574px 0.819px 5px 0px rgba(204, 204, 204, 1);
        position: relative;
        margin-left: 10px;
        .title {
          width: 269px;
          height: 122px;
          opacity: 1;
        }
        .play-picture {
          position: absolute;
          top: 22%;
          left: 40%;
          font-size: 45px;
        }
        .myVideoList_text {
          width: 100%;
          height: 40px;
          margin: 0;
          overflow: hidden;
          p {
            height: 19px;
            margin: 0;
            font-family: Adobe Heiti Std;
            letter-spacing: 0px;
            margin-left: 8px;
          }
          p:nth-child(1) {
            font-size: 12px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          p:nth-child(2) {
            font-size: 10px;
          }
        }
      }
    }
  }
}
</style>
